import React from "react"
import Layout from "../components/layout/layout"
import { graphql } from "gatsby"

import Footer from "../components/footer/footer"
import Navbar from "../components/navbar/navbar"
import style from "./post.module.css"


export default ({ data }) => {
  const post = data.sitePage;
  // const img = require(`../components/media/${post.frontmatter.img}`)

  return (
    <Layout>
      <Navbar />
      <div className="container">
        <div className="row">
          <div className={`${style.post}`}>
            <div className={`${style.content}`}>
              <h1 className={`${style.title}`}>{post.context.titolo}</h1>
              {(post.context.mainImage) ? (
                <img src={`https:${post.context.mainImage.fields.file.url}`} alt=""/>
              ) : null}
              <div dangerouslySetInnerHTML={{ __html: post.context.data }} />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    sitePage(context: { slug: {eq: $slug} } ) {
      context {
        data
        titolo
        mainImage {
          fields {
            file {
              url
            }
          }
        }
      }
    }
  }
`
